.loadingpage {
    background-color: rgba(245, 245, 245, 0.5);
    backdrop-filter: blur(10px);

    width: 100%;
    min-height: 100vh;

    position: absolute;
    top: 0;
    left: 0;
    padding-top: 55px;
}

.loadingpage section {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: black;
}